//estructura de la card
import React, {useState, useEffect} from 'react';
import ReactCardFlip from 'react-card-flip';
import backFace from '../images/question.png';

const Card = ({name, number, frontFace, flipCard, unflippedCards, disableCards, disabledCards}) => {
    const [isFlipped, setIsFlipped] = useState(false);//de la libreria flip, el estado inicial es falso porque la carta No está volteada
    const [hasEvent, setHasEvent] = useState(true);
    useEffect(()=>{
        if(unflippedCards.includes(number)){
            setTimeout(()=> setIsFlipped(false), 700);
        }
    },[unflippedCards]) //cada vez que se ejecuta inflippedCards se va a ejecutar el useEffect

    useEffect(()=>{
        if(disableCards.includes(number)){
            setHasEvent(false);
        }
    },[disabledCards])

    const handleClick = e => {
        const value = flipCard(name, number); //esta funcion me regresa 0 si esta tratando de clickear la misma carta y 1 si esta clickenado otra carta
        if(value !== 0 ){ //si es diferente a 0 la carta se volteará 
            setIsFlipped(!isFlipped);
        }
        
    }
        return(
            <div className="card">
               <ReactCardFlip isFlipped={isFlipped}>
                    <img className="card-image" src={backFace} alt="back-face" onClick={hasEvent ? handleClick : null}/>
                    <img className="card-image" src={frontFace} alt="front-face" onClick={hasEvent ? handleClick : null}/>
               </ReactCardFlip>
            </div> 
        )
}
export default Card;