import React, { useState, useEffect } from 'react';
import './App.scss';
import Card from './components/Card';
import { images } from './import';

function App() {
  
    //uso de hooks 
    const [cards, setCards] = useState([]);
    const [firstCard, setFirstCard] = useState({});
    const [secondCard, setSecondCard] = useState({});
    const [unflippedCards, setUnflippedCards] = useState([]); //contiene los numeros de las cartas que deben retornar a su posicion original
    const [disableCards, setDisableCards] = useState([]);//contiene numeros de cartas que deben ser desabilitadas porque ya hicieron match 
    const [matchedCards, setMatchedCards] = useState(0); //cantidad de cartas que han hecho match para avisar cuando el usuario ha ganado
    // useEffect permite ejecutar una funcion despues de que se termina de renderizar, actualizar o desmontar el componente, en este caso sera al renderizar, el primer parametro sera la funcion a ejecutar y el segundo sera un arreglo de dependencias 
    useEffect(()=>{
      shuffleArray(images);
      setCards(images); //seteo las cards, de un arreglo vacio, a las imagenes
    },[])//como quiero que se ejecute una sola vez, le paso un arreglo vacio como dependencia...

    useEffect(()=>{
      checkForMatch();
    },[secondCard])

    //funcion para desordenar las cartas
    const shuffleArray = (array) => {
      for(let i = array.length -1; i > 0; i-- ){
        let j = Math.floor (Math.random() * (i + 1));
        let temp = array[i];
        array[i] = array[j];
        array[j] = temp;
      }
    }

    //funcion para voltear las cartas
    const flipCard = (name, number) => {
      if (firstCard.name === name && firstCard.number === number){
        return 0; //quiere decir que esta tratando de voltear la misma carta 
      }
      if (!firstCard.name){ //si al hacer click en una carta y el atributo name de la primera carta no existe es porque no se ha seleccionado nada antes, entonces creo la primera carta 
        setFirstCard({ name, number});
      }else if (!secondCard.name){
        setSecondCard({name, number});
      }
      return 1;
    }
    //funcion para comparar cartas 
    const checkForMatch = () => {
      if (firstCard.name && secondCard.name){
        const match = firstCard.name === secondCard.name;
        match ? disabledCards() : unflipCards();
      }
    }

    const disabledCards = () =>{
      setDisableCards([firstCard.number, secondCard.number]);
      setMatchedCards(matchedCards + 1);
      resetCards();
    }

    const unflipCards = () =>{
      setUnflippedCards([firstCard.number, secondCard.number]);
      resetCards();
    }

    const resetCards = () =>{ //si al releccionar dos cartas no hacen match, reseteo el estado para que firstCard y secondCard queden vacios nuevamente
      setFirstCard({});
      setSecondCard({});
    }

    const playAgain = () =>{
      window.location.reload();
    }

  return (
    <div className="app">
      
        {matchedCards === cards.length / 2 ? 
        <div className="container-title">
          <h1 className="you-winner">You´re Winner!!!</h1>
          <button onClick={playAgain}>Play again</button> 
        </div> 
        :
        <div className="container-title"> 
          <h1>Let´s play!</h1>
        </div>
        }
          <div className="cards-container">
            {
              cards.map((card, index)=>(
                <Card 
                key= {card.id}
                name={card.emoji} 
                number={index} 
                frontFace={card.src} 
                flipCard={flipCard}
                unflippedCards={unflippedCards}/* cartas que deberian regresar a su posicion original, no hicieron match */
                disableCards={disableCards}
                disabledCards={disabledCards}/* cartas bloqueadas que ya hicieron match */
                />
              ))
            } 
          </div>
    </div>
  );
}

export default App;
